 .menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;
    &__top {
      margin-bottom: 32px;
    }
    
    &__nav {
      margin-bottom: 40px;
    }
  
    &__content {
      @include contentPaddingInline;
    }
  
    &__phone-number {
      display: block;
      margin-bottom: 16px;
  
      color: $color--black;
      text-decoration: none;
      font-size: 16px;
      line-height: 27px;
    }
  
    &__call-to-order {
      display: block;
      width: fit-content;
      padding-bottom: 7px;
      border-bottom: 1px solid $color--black;
  
      color: $color--black;
      text-decoration: none;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
    }
 }
