 .icon {
   display: block;
   height: 24px;
   width: 24px;
   background-size: cover;

   @include onTablet {
    height: 32px;
    width: 32px;
   }

  &--phone {
      background-image: url(../images/icons/icon-phone-call.png);
    }
  
  &--menu {
    background-image: url(../images/icons/icon-burger-menu.png);
  }

  &--close {
    background-image: url(../images/icons/icon-close.png);
  }
 }
