.page {
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 22.4px;
  color: #FFFFFF;

  scroll-behavior: smooth;

  &__body {
      margin: 0;
      padding: 0;
  }
  
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
    }
  }
}
